import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Index from "../views/default"
import BlogDetail from "../components/blogDetaili"
import About from "../components/about"
import MyMap from "../components/myMap";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path:"/blogdetail/:id",
    name:"blogdetail",
    component: require => require(BlogDetail)
  },
  {
    path:"/about",
    name:"About",
    component:About
  },
  {
    path:"/map",
    name:"Map",
    component:require => require(MyMap)
  },
  {
    path:"*",
    redirect:"/"
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
