<template>
  <div class="card">
    <h2>猜你喜欢</h2>
    <ul>
      <li v-for="item in titleList" :key="item.id">
        <img :src="item.img" />
        <div class="con">
          <!-- <h3>{{ item.title }}</h3> -->
          <router-link tag="h3" :to="{name:'blogdetail',params:{bid:item.id}}">{{
          item.title
        }}</router-link>
          <div class="info">
            <span>博客|</span>
            <span>{{ item.ctime | dateSet }}</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      titleList: []
    };
  },
  mounted() {
    var that = this;
    axios({
      method: "get",
      url: "/queryHotBlog"
    })
      .then(function(res) {
        var result = [];
        for (var i = 0; i < res.data.data.length; i++) {
          var temp = {};
          temp.title = res.data.data[i].title;
          temp.link = "/blog_detail.html?bid=" + res.data.data[i].id;
          temp.img = res.data.data[i].img;
          temp.id = res.data.data[i].id;
          temp.ctime = res.data.data[i].ctime;
          result.push(temp);
        }
        that.titleList = result;
      })
      .catch(function(err) {
        console.log(err);
      });
  }
};
</script>
<style lang="scss" scoped>
.card {
  width: 100%;
  h2 {
    font-size: 20px;
    font-weight: 500;
    color: #666;

    border-bottom: 2px solid #008c8c;

    margin-bottom: 10px;
  }
  ul {
    li {
      width: 100%;
      height: 80px;
      display: flex;
      margin-bottom: 10px;
      img {
        width: 70px;
        height: 70px;
        border-radius: 5px;
      }
      .con {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding: 5px 8px;
        h3 {
          font-size: 15px;
          cursor: pointer;
        }
        .info {
          font-size: 14px;
        }
      }
    }
    
  }
}
</style>
