<script>
export default {
  data(){
    return{
      view:0,
      comment:0
    }
  },
  mounted() {
    this.axios({
      methods:"get",
      url:"/queryAllCommentsCount"
    }).then(res=>{
      this.comment = res.data.data[0].count
    })
    this.axios({
      methods:"get",
      url:"/queryAllViewsCount"
    }).then(res=>{
      this.view = res.data.data[0]["sum(views)"];
    })
  },
}
</script>
