<template>
  <div class="blog_comments" id="blog_comments">
    <span class="blog_comments_msg">关于：目前有{{ total }}条留言</span>
    <div class="blog_comment" :key="comment.ctime" v-for="comment in comments">
      <span
        ><b>{{ comment.user_name }}</b
        >：{{ comment.options }}发表于{{ comment.ctime | dateSet
        }}<a
          style="cursor: pointer;"
          @click="reply(comment.id, comment.user_name)"
          >[回复]</a
        ></span
      >
      <div>{{ comment.comments }}</div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import bus from "./bus";
export default {
  data() {
    return {
      total: 0,
      comments: []
    };
  },
  props: ["_bid"],
  methods: {
    reply: function(commentId, userName) {
      let data = {
        commentId,
        userName
      };
      bus.$emit("reply", data);
    }
  },
  created() {
    var bid = this._bid;
    axios({
      method: "get",
      url: "/queryCommentsByBlodId?bid=" + this._bid
    }).then(res => {
      this.comments = res.data.data;
      for (var i = 0; i < this.comments.length; i++) {
        if (this.comments[i].parent > -1) {
          this.comments[i].options = "回复@" + this.comments[i].parent_name;
        }
      }
    });
    axios({
      method: "get",
      url: "/queryCommentsCountByBlogId?id=" + this._bid
    })
      .then(res => {
        this.total = res.data.data[0].count;
      })
      .catch(function(err) {
        console.log(err);
      });
  }
};
</script>
<style scoped>
.blog_comments {
  width: 100%;
}

.blog_comments_msg {
  margin-top: 20px;
  padding: 10px;
  display: inline-block;
  width: 100%;
  text-align: left;
}

.blog_comment {
  width: 95%;
  background: #d9edf7;
  border: 1px dashed #7ca4c1;
  padding: 10px 10px 20px 20px;
  color: #31708f;
  margin-top: 10px;
  text-align: left;
}

.blog_comment div {
  display: inline-block;
  width: 100%;
  padding-left: 10px;
  margin-top: 10px;
}
</style>
