<template>
  <div class="article-container">
    <div class="article" v-for="article in articleList" :key="article.ctime">
      <div class="img-box">
        <img :src="article.img" />
      </div>
      <div class="con">
        <router-link
          tag="h2"
          :to="'/blogdetail/' + article.id"
          class="article-title"
          >{{ article.title }}</router-link
        >
        <p class="sub-title">{{ article.subtitle }}</p>
        <ul class="article-footr">
          <li>
            <i class="iconfont icon-caidanguanli_"></i>
            <span>{{ article.tags }}</span>
          </li>
          <li>
            <i class="iconfont icon-riqi"></i>
            <span>{{ article.date | dateSet }}</span>
          </li>
          <li>
            <i class="iconfont icon-jiaoyin"></i>
            <span>{{ article.views }}</span>
          </li>
        </ul>
      </div>
    </div>

    <div class="page_tool">
      <ul>
        <li
          v-for="pageNum in pageNumList"
          v-on:click="jumpTo(pageNum.page)"
          v-bind:class="{ now_page: pageNum.text == page ,disabled: pageNum.page <= 1 ||  pageNum.page >= page,tool:pageNum.class}"
          :key="pageNum.text"
        >
          {{ pageNum.text }}
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import bus from "./bus";
import { watch } from "fs";
import { async } from "q";
export default {
  data() {
    return {
      page: 1,
      pageSize: 12,
      count: 1,
      articleList: [],
      pageNumList: [{}],
      tag: ""
    };
  },
  updated() {
    //获取dom和视口高度,
    this.domList = document.getElementsByClassName("article");
    this.windowHight = document.documentElement.clientHeight;
    this.animation();
  },
  methods: {
    dateSet(num) {},
    //列表动画
    animation() {
      for (let i = 0; i < this.domList.length; i++) {
        if (
          this.domList[i].getBoundingClientRect().top <
          this.windowHight + 30
        ) {
          this.domList[i].classList.remove("fadeOutDown");
          this.domList[i].classList.add("fadeInUp");
        } else {
          this.domList[i].classList.remove("fadeInUp");
          this.domList[i].classList.add("fadeOutDown");
        }
      }
    },
    // 根据tag获取文章列表
    getBlogByTag(page=this.page) {
      this.axios({
        method: "get",
        url:
          "/queryByTag?tag=" +
          this.tag +
          "&page=" +
          (page - 1) +
          "&pagesize=" +
          this.pageSize
      }).then(res => {
        var result = res.data.data;
        var list = [];
        for (var i = 0; i < result.length; i++) {
          var temp = {};
          temp.title = result[i].title;
          temp.content = result[i].content;
          temp.date = result[i].ctime;
          temp.views = result[i].views;
          temp.tags = result[i].tags;
          temp.id = result[i].id;
          temp.subtitle = result[i].subtitle;
          temp.img = result[i].img;
          temp.link = "/blog_detail.html?bid=" + result[i].id;
          list.push(temp);
        }
        this.articleList = list;
        this.page = page;
      });
    },

     getBlogList(page=this.page) {
      if (this.tag === "") {
         this.getBlogCount(page);
        this.getPage();
      } else {
         this.getCountByTag(page);
        this.getBlogByTag(page);
      }
    },
    // 根据tag获取文章总数
    getCountByTag(tagId) {
      this.axios({
        method: "get",
        url: "/queryBlogCountByTag?tagId" + tagId
      }).then(res => {
          this.count = res.data.data[0].count;
        }).catch(err => {
          console.log(err);
        });
    },
    //获取文章
    getPage(page = this.page) {
      this.axios({
        method: "get",
        url:
          "/queryBLogByPage?page=" + (page - 1) + "&pageSize=" + this.pageSize
      })
        .then(res => {
          var result = res.data.data;
          var list = [];
          for (var i = 0; i < result.length; i++) {
            var temp = {};
            temp.title = result[i].title;
            temp.content = result[i].content;
            temp.date = result[i].ctime;
            temp.views = result[i].views;
            temp.tags = result[i].tags;
            temp.id = result[i].id;
            temp.img = result[i].img;
            temp.subtitle = result[i].subtitle;
            list.push(temp);
          }
          this.articleList = list;
        })
        .catch(function(err) {
          console.log(err);
        });
      this.page = page;
      this.generatePageTool();
    },
    //获取文章总数
    getBlogCount() {
      this.axios({
        method: "get",
        url: "/queryBlogCount"
      }).then(res => {
        this.count = res.data.data[0].count;
        this.generatePageTool();
      });
    },
    // 计算分页
    generatePageTool() {
      var that = this;
      var nowPage = this.page;
      var pageSize = this.pageSize;
      var totalCount = this.count;
      var result = [];
      result.push({ text: "第一页", page: 1 ,class:true});
      result.push({ text: "上一页", page: nowPage - 1 ,class:true});
      if (nowPage > 2) {
        result.push({ text: nowPage - 2, page: nowPage - 2 });
      }
      if (nowPage > 1) {
        result.push({ text: nowPage - 1, page: nowPage - 1 });
      }
      result.push({ text: nowPage, page: nowPage });
      if (nowPage + 1 <= (totalCount + pageSize - 1) / pageSize) {
        result.push({ text: nowPage + 1, page: nowPage + 1 });
      }
      if (nowPage + 2 <= (totalCount + pageSize - 1) / pageSize) {
        result.push({ text: nowPage + 2, page: nowPage + 2 });
      }
      result.push({ text: "下一页", page: nowPage + 1 ,class:true});

      result.push({
        text: "尾页",
        class:true,
        page: parseInt((totalCount + pageSize - 1) / pageSize)
      });
      that.pageNumList = result;
      return result;
    },
    // 分页跳转
    jumpTo: function(page) {
      if(page >= this.page || page <= 1){
        return
      }
      this.getBlogList(page);

      this.goTop();
    },
    //回到顶部动画
    goTop() {
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      document.body.style.marginTop = -scrollTop + 150 + "px";
      document.body.scrollTop = 0;
      document.body.style.transition = "all .7s ease-in-out";
      document.body.style.marginTop = 0;
      setTimeout(function() {
        document.body.style.transition = "none";
      }, 3000);

      document.body.scrollTop = document.documentElement.scrollTop = 0;
    }
  },
  mounted() {
    // this.getPage(this.page);
    // this.getBlogCount();
    this.getBlogList();
    bus.$on("getTag", tag => {
      this.tag = tag;
    });
    window.onscroll = _ => {
      this.animation(this.domList);
    };
  },

  watch: {
    tag(val, old) {
      this.page = 1;
      if (val == "") {
        this.getPage();
      }
      this.getBlogList(1);
    }
  }
};
</script>
<style lang="scss">
.article-container {
  width: 880px;
  padding: 8px;
}
.article {
  overflow: hidden;
  &.fadeOutDown {
    animation: fadeOutDown 1s forwards;
    @keyframes fadeOutDown {
      from {
        opacity: 1;
      }

      to {
        opacity: 0;
        -webkit-transform: translate3d(0, 100%, 0);
        transform: translate3d(0, 100%, 0);
      }
    }
  }
  &.fadeInUp {
    animation: fadeInUp 1s forwards;
    @keyframes fadeInUp {
      from {
        opacity: 0;
        -webkit-transform: translate3d(0, 100%, 0);
        transform: translate3d(0, 100%, 0);
      }

      to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }
    }
  }
  margin-top: 25px;
  width: 100%;
  height: 130px;
  display: flex;
  list-style: none;
  &:hover {
    img {
      transform: scale(1.3);
    }
  }
}
.article .img-box {
  width: 260px;
  height: 130px;
  overflow: hidden;
  img {
    width: 260px;
    height: 130px;
    transition: all 0.4s;
  }
}
.article .con {
  width: 600px;
  height: 130px;
  padding: 7px 14px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.article .con .article-title {
  font-size: 20px;
  color: #666;
  cursor: pointer;
  transition: all 0.4s;
  &:hover {
    transform: translate(10px, 0);
    color: rgb(17, 203, 236);
  }
}
.article .con .sub-title {
  overflow: hidden;
  height: 55px;
  font-size: 16px;
  color: #797474;
}

.article .con .article-footr {
  display: flex;
  color: #797474;
}
.article .con .article-footr li {
  list-style: none;
  margin: 0 10px;
}
.article .con .article-footr li i {
  margin-right: 8px;
}

.page_tool {
  margin-top: 20px;
  display: inline-block;
}
.page_tool > ul {
  margin: 0 auto;
}
.page_tool > ul > li {
  display: inline-block;
  list-style: none;
  width: 60px;
  height: 30px;
  background: pink;
  line-height: 30px;
  background: white;
  text-align: center;
  border: 1px solid #ccc;
  margin-right: 20px;
  border-radius: 8px;
  // float: left;
  cursor: pointer;
}

.page_tool > ul > li:hover {
  background: #2b86e1;
}
.now_page {
  background: #2b86e1 !important;
  color: #fff !important;
  font-size: 20px;
}
.disabled.tool{
  background: #ccc !important;
  cursor: crosshair !important;
}
</style>
