<template>
  <div class="send-comment" id="send-comment">
    <span>发表评论</span>
    <div v-if="replyName != 0">
      <span
        >正在回复{{ replyName }} <button @click="exit">取消回复</button>
      </span>
    </div>
    <div>
      <input v-model="name" type="text" placeholder="昵称" id="comment_name" />
      <input
        v-model="email"
        type="text"
        placeholder="邮箱 (评论被回复是你能收到通知)"
        id="comment_email"
      />
    </div>
    <div>
      <textarea
        v-model="content"
        id="comment_content"
        placeholder="无意义的内容我可能不会回复你"
      ></textarea>
    </div>
    <div>
      <input type="text" placeholder="验证码" v-model="inpCode" />
      <span v-html="vcode" @click="changeCode"></span>
    </div>
    <button v-on:click="sendComment()">提交留言</button>
  </div>
</template>

<script>
import axios from "axios";
import bus from "./bus";
export default {
  name: "sendComment",
  data() {
    return {
      vcode: "",
      rightCode: "",
      inpCode: "",
      replyName: "0",
      replyid: -1,
      name: "",
      email: "",
      content: ""
    };
  },
  props: ["_bid"],
  mounted() {
    bus.$on("reply", data => {
      (this.replyName = data.userName), (this.replyid = data.commentId);
    });
  },
  methods: {
    changeCode() {
      axios({
        method: "get",
        url: "/queryRandomCode"
      }).then(res => {
        this.vcode = res.data.data.data;
        this.rightCode = res.data.data.text;
      });
    },
    sendComment() {
      if (this.inpCode.toLocaleLowerCase() != this.rightCode.toLocaleLowerCase()) {
        alert("验证码错误");
        this.inpCode = "";
        this.changeCode();
        return;
      }
      axios({
        method: "get",
        url:
          "/addComment?bid=" +
          this._bid +
          "&parent=" +
          this.replyid +
          "&userName=" +
          this.name +
          "&email=" +
          this.email +
          "&content=" +
          this.content +
          "&parentName=" +
          this.replyName
      })
        .then(res => {
          alert(res.data.msg);
          this.changeCode()
          this.content = "";
          this.inpCode = "";
        })
        .catch(function(err) {
          alert("评论失败,或许是网络不通畅");
        });
    },
    exit() {
      this.replyName = "";
      this.replyid = -1;
    }
  },
  created() {
    this.changeCode();
  }
};
</script>

<style scoped>
.send-comment {
  background: #f5f5f5f5;
  width: 96%;
  border-radius: 3px 4px 3px;
  text-align: left;
  margin: 50px 0px 20px 0px;
  padding: 15px;
}

.send-comment input {
  width: 300px;
  height: 34px;
  border: 1px solid #ccc;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  margin-top: 10px;
  padding-left: 5px;
  margin-bottom: 10px;
  font-size: 14px;
}

.send-comment textarea {
  width: 90%;
  height: 100px;
  border: 1px solid #ccc;
  padding: 5px;
  margin-top: 10px;
  font-size: 14px;
}

.send-comment button {
  width: 100px;
  height: 30px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 10px;
  font-size: 14px;
}
.send-comment button:hover {
  color: white;
  background: black;
}

.blog_comments {
  width: 100%;
  /*background-color: pink;*/
}

.blog_comments_msg {
  margin-top: 20px;
  padding: 10px;
  display: inline-block;
  width: 100%;
  text-align: left;
}

.blog_comment {
  width: 95%;
  background: #d9edf7;
  border: 1px dashed #7ca4c1;
  padding: 10px 10px 20px 20px;
  color: #31708f;
  margin-top: 10px;
  text-align: left;
}

.blog_comment div {
  display: inline-block;
  width: 100%;
  padding-left: 10px;
  margin-top: 10px;
}
</style>
