<template>
  <div class="card">
    <h2>最新评论</h2>
    <div class="con">
      <div v-for="comment in commentList" :key="comment.id" class="item">
        <h5>
          <span class="title">{{ comment.name }}</span>
          <span class="date">{{ comment.date | dateSet }}</span>
        </h5>
        <p>{{ comment.comment }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "newComment",
  data() {
    return {
      commentList: [
        {
          name: "this is username",
          date: "2018-10-10",
          comment: "this is a big 巴拉巴拉巴拉巴拉把那零八零八"
        }
      ]
    };
  },
  mounted() {
    var that = this;
    axios("/queryNewComments")
      .then(function(resp) {
        var result = [];
        for (var i = 0; i < resp.data.data.length; i++) {
          var temp = {};
          temp.name = resp.data.data[i].user_name;
          temp.date = resp.data.data[i].ctime;
          temp.comment = resp.data.data[i].comments;
          result.push(temp);
        }
        that.commentList = result;
      })
      .catch(function(err) {
        console.log(err);
      });
  }
};
</script>
<style lang="scss" scoped>
.card {
  width: 100%;
  h2 {
    font-size: 20px;
    font-weight: 500;
    color: #666;

    border-bottom: 2px solid #008c8c;

    margin-bottom: 10px;
  }
  .con {
    width: 100%;

    .item {
      border-bottom: 1px solid #ccc;
    }
    h5 {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }
}
</style>
