<template>
    <div class="container">
        <div class="title">个人博客's SiteMap</div>
        <div class="link"><router-link to="/">个人博客</router-link> >> 站点地图</div>
        <div class="blog_list" id="blog_list">
            <span>最新文章</span>
            <ul>
                <li v-for="blog in blogList" :key="blog.id"><a v-bind:href="blog.link">{{blog.title}}</a></li>
            </ul>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    export default {
        name: "myMap",
        data(){
            return {
                blogList:[]
            }

        },
        computed:{

        },
        created(){
            var that=this;
            axios({
                method:"get",
                url:"/queryAllBlog"
            }).then(function (res) {
                for (var i = 0;i < res.data.data.length;i ++){
                    res.data.data[i].link="/blog_detail.html?bid=" + res.data.data[i].id
                }
                that.blogList=res.data.data
            })
        }
    }
</script>

<style scoped>
    .container{
        width: 880px;
        display: flex;
        flex-direction: column;
    }
    .title{
        width: 100%;
        text-align: center;
        font-size: 20px;
        font-weight: 600;
        line-height: 50px;
    }
    .link{
        border: 1px solid #eeeeee;
        padding: 10px;
        width: 95%;
        margin: 0px auto;
    }

    .link > a{
        text-decoration: none;
        font-weight: 700;
        color: #000;
    }

    .blog_list{
        display: flex;
        flex-direction: column;
        border: 1px solid #eeeeee;
        padding: 10px;
        width: 95%;
        margin: 10px auto;
        font-size: 14px;
        font-weight: 600;
    }
    .blog_list span{
        display: block;
    }
    .blog_list ul{
        padding: 10px 10px;
        font-size: 14px;
        font-weight: 600;
        width: 100%;
        margin: 10px auto;
    }

    .blog_list li{
        /*list-style: none;*/
        margin: 5px;
    }
    .blog_list a{
        text-decoration: none;
        color: black;
        cursor: pointer;
        font-weight: 400;
    }
</style>