<template>
  <div class="card">
    <h2>随机标签云</h2>
    <div class="con">
      <span
        v-for="tag in tags"
        :key="tag.id"
        @click="tagChange(tag.id)"
        v-bind:style="{ color: randomColor(), fontSize: randomSize() }"
        >{{ tag.text }}</span>
      
    </div>
  </div>
</template>
<script>
import bus from "./bus"
export default {
  data() {
    return {
      tags: []
    };
  },
  computed: {
    randomColor() {
      return function() {
        var red = Math.random() * 255 + 50;
        var green = Math.random() * 255 + 50;
        var blue = Math.random() * 255 + 50;
        return `rgb(${red},${green},${blue})`;
      };
    },
    randomSize() {
      return function() {
        var size = Math.random() * 20 + 12 + "px";
        return size;
      };
    }
  },
  created() {
    var that = this;
    this.axios({
      method: "get",
      url: "/queryRandomTags"
    })
      .then(function(res) {
        // console.log(res);
        var result = [];
        for (var i = 0; i < res.data.data.length; i++) {
          result.push({
            text: res.data.data[i].tag,
            link: "/?tag=" + res.data.data[i].id,
            id:res.data.data[i].id
          });
        }
        that.tags=result;
      })
      .catch(function(err) {
        console.log(err);
      });
  },
  methods:{
    tagChange(tag){
      // console.log(tag)
      bus.$emit("getTag",tag)
    }
  }
};
</script>
<style lang="scss" scoped>
.card {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 20px;
  h2 {
    font-size: 20px;
    font-weight: 500;
    color: #666;
    border-bottom: 2px solid #008c8c;
    margin-bottom: 10px;
  }
  .con {
    width: 300px;
    span {
      display: inline-block;
      margin-right: 10px;
      text-decoration: none;
      cursor: pointer;
    }
  }
}
</style>
