<template>
  <div class="container">
    <div class="blog" id="blog_detail">
      <div class="blog_title">{{ title }}</div>
      <div class="blog_msg">作者: Cherry 标签 {{ tags }} 浏览({{ views }})</div>
      <div class="blog_content" v-html="content"></div>
    </div>
    <comment :_bid="this.bid"/>
    <send-comment :_bid="this.bid"/>
  </div>
</template>

<script>
import axios from "axios";
import sendComment from "./sendComment"
import comment from "./comment.vue"
export default {
  name: "blogdetail",
  components: {
    sendComment,
    comment
  },
  data() {
    return {
      title: "",
      content: "",
      ctime: "",
      tags: "",
      views: "",
      bid:""
    };
  },
  mounted() {
    
  },
  created() {
    var that = this;
    this.bid = this.$route.params.id;
    this.axios({
      method: "get",
      url: "/queryBlogById?bid=" + this.bid
    })
      .then(function(res) {
        var result = res.data.data[0];
        that.title = result.title;
        that.content = result.content;
        that.ctime = result.ctime;
        that.tags = result.tags;
        that.views = result.views;
      })
      .catch(function(err) {
        console.log(err);
      });
  }
};
</script>

<style lang="scss" scoped>
.container {
    display: flex;
    flex-direction: column;
  width: 880px;
  padding: 8px;
}
.blog {
  background: white;
  width: 800px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  padding: 10px 15px;
  box-shadow: 3px 4px 3px #888888;
  text-align: left;
}
.blog_title {
  font-size: 24px;
  font-weight: 600;
}
.blog_msg {
  padding-top: 10px;
  padding-bottom: 20px;
  border-bottom: 1px solid gray;
  margin-bottom: 20px;
}
.blog_content {
  padding: 0px 10px 20px 0px;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
  -ms-word-break: break-all;
  word-break: break-all;
  overflow: hidden;
}
</style>
